html,
body {
  min-width: 320px;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 26px;
  overflow-x: hidden;
  color: #484848; }

img {
  width: 100%;
  height: auto; }

.form-error {
  position: absolute;
  color: #ff0000; }

.modals__fieldset .form-error {
  position: static; }

@media (max-width: 768px) {
  .form-error {
    position: static;
    margin-top: 0;
    text-align: center; } }

.slider,
.slider--testimonials {
  position: relative;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  overflow: hidden;
  box-sizing: border-box;
  z-index: 1; }

.slider__list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  z-index: 1; }

.slider__testimonials-list {
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  z-index: 1; }

.slider__item {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  width: 250px;
  box-sizing: border-box; }

.slider__testimonials-item {
  position: relative;
  flex-shrink: 0;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  width: 100%; }

.slider__testimonials-pagination {
  font-family: "Fira Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 150%;
  color: rgba(72, 72, 72, 0.5); }

.slider__testimonials-btn-prev,
.slider__testimonials-btn-next {
  width: 25px;
  height: 25px;
  padding: 0;
  border: none;
  background: none;
  cursor: pointer; }

.slider__testimonials-btn-prev {
  background: url("../img/icon-arrow-left.svg") center no-repeat;
  margin-top: -2px;
  margin-right: 0;
  margin-left: 16px; }

.slider__testimonials-btn-next {
  background: url("../img/icon-arrow-right.svg") center no-repeat;
  margin-top: -2px;
  margin-left: 4px;
  margin-right: auto; }

.slider__bullets {
  margin-top: 4px;
  text-align: center; }

.slider__bullet {
  width: 6px;
  height: 6px;
  margin: 0;
  margin-right: 8px;
  padding: 0;
  border: none;
  border-radius: 10px;
  background: #484848;
  opacity: 0.2; }
  .slider__bullet:last-child {
    margin-right: 0; }
  .slider__bullet--active {
    position: relative;
    top: 1px;
    left: -1px;
    width: 8px;
    height: 8px;
    background: linear-gradient(108.42deg, #ffc341 20.46%, #ffd701 65.31%);
    opacity: 1; }

@media (max-width: 1200px) {
  .slider__list {
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    z-index: 1; }
  .slider__item,
  .slider__testimonials-item {
    position: relative;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    width: 100%;
    box-sizing: border-box; } }

@media (min-width: 1201px) {
  .slider__bullets {
    display: none; } }

.top-bar {
  background-color: #f8f8f8;
  font-size: 14px; }

.top-bar__nav {
  width: 1240px;
  margin: 0 auto;
  padding: 5px 10px; }

.top-bar__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: flex-end; }

.top-bar__nav-item {
  margin-left: 20px; }

.top-bar__nav-link {
  display: flex;
  align-items: center;
  padding: 5px;
  text-decoration: none;
  color: #333333;
  transition: color ease 0.3s; }
  .top-bar__nav-link:hover, .top-bar__nav-link:focus {
    color: #dd3939; }

.top-bar__nav-icon {
  position: relative;
  top: -2px;
  fill: #34495e;
  width: 15px;
  height: 15px;
  margin-right: 5px;
  transition: fill ease 0.3s; }

.top-bar__nav-link:hover .top-bar__nav-icon,
.top-bar__nav-link:focus .top-bar__nav-icon {
  fill: #dd3939; }

@media (max-width: 375px) {
  .top-bar {
    display: none; } }

@media (min-width: 375px) and (max-width: 1240px) {
  .top-bar {
    font-size: 13px; }
  .top-bar__nav {
    width: 100%;
    padding: 5px 5px; }
  .top-bar__nav-list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: center;
    flex-wrap: wrap; }
  .top-bar__nav-item {
    margin: 0 10px 0 10px; }
  .top-bar__nav-link {
    display: flex;
    align-items: center;
    padding: 0px 5px; }
  .top-bar__nav-icon {
    position: relative;
    top: -1px;
    fill: #34495e;
    width: 13px;
    height: 13px;
    margin-right: 5px;
    transition: fill ease 0.3s; } }
